/*storyTitle start*/
.storyTitle{
	background-image: url('../image/coffee_heart.jpg');
	width: 100%;
  background-size: cover;
	margin: 95px 0px 0px;
	padding-top: 220px;
	padding-bottom: 220px;
	position: relative;
	overflow: hidden;
	transition: opacity 1.5s;
	animation: bgmove 12s infinite linear;

	h3{
		color: #fff;
		font-size: 20px;
		font-weight: bold;
		font-family: 微軟正黑體, Helvetica, roboto, arial, sans-serif;
	}
	.textbox{
		div:nth-of-type(1){
			margin-top: 10px;
		}
	}
	.abc figure{
		width: 25px;
		height: 25px;
		display: inline-block;
		margin-top: 10px;
	}
	
	img	{
		width: 100%;
		vertical-align: middle;
	}
	p{
		display: inline-block;
		margin-top: 10px;
		margin-left: 10px;
		color: #fff;
		font-size: 16px;
		font-family: 微軟正黑體, Helvetica, roboto, arial, sans-serif;
	}

	@include desktop(){
		margin: 110px 0px 0px;
		padding-top: 400px;
		padding-bottom: 400px;
		animation: bgmove 10s infinite linear;
	}

	@include ipadPro(){
		margin: 95px 0px 0px;
		padding-top: 180px;
		padding-bottom: 180px;
		animation-duration: 7s;
	}

	@media (max-width: 823px){
		margin: 65px 0px 0px;
	}

	@include ipad(){
		padding-top: 150px;
		padding-bottom: 150px;
		animation-duration: 7s;
		z-index: -99;
	}

	@media (max-width: 622px){
		margin: 65px 0px 0px;
		padding-top: 90px;
		padding-bottom: 90px;
		animation-duration: 7s;
		.abc{
			display: none;
		}
	}

	@include iphone8(){
		margin: 80px 0px 0px;
		padding-top: 75px;
		padding-bottom: 75px;
		animation-duration: 7s;
	}

	@include iphone5(){
		padding-top: 60px;
		padding-bottom: 60px;
		animation-duration: 7s;
	}
}

