/*bsTitle start*/
.bsTitle{
	width: 100%;
	padding-bottom: 50px;
	background: url(../image/bg/bg_about.png);
	background-attachment: fixed;
	background-size: cover;
	background-position: center 50px;
	p{
		text-align: center;
		font-size: 3.5vmin;
		font-family: 微軟正黑體, sans-serif;
	}

	@include ipadPro(){
		background-size: cover;
		background-position: 0px 0px;
	}

	@include ipad(){
		background-size: cover;
		background-position: 0px 0px;
	}

	@include iphone8(){
		background-size: cover;
		background-position: -350px 0px;
	}
}
.about,.feature,.knowledge,.con{
	padding: 20px;
	padding-top: 60px;
	margin: auto;
	text-align: center;
	em{
		font-family: 'Dosis', sans-serif;
		font-style: unset;
		font-weight: normal;
		font-size: 6vmin;
	}
	strong{
		font-size: 2.7vmin;
		margin-left: 10px;
		padding-right: 30px;
		padding-top: 20px;
		letter-spacing: 5px;
		font-weight: normal;
		background: url(../image/beans.png) no-repeat; 
		background-position: 100% 0px;	
		background-size: 30px 30px;
	}
	@include iphone8(){
		em{
			font-size: 24px;
		}
		strong{
			font-size: 14px;
		}
	}
	
}
.about_pic{
		width: 90%;
		margin-top: 50px;
		margin-left: auto;
		margin-right: auto;
		div{
			width: 40%;
			padding: 10px;
			margin: 2% 0 0 6.5%;
			background: #fff;
			display: inline-block;
			position: relative;
			@include ipadPro(){
				width: 45%;
				margin: 0;
				margin-left: 3%;
				margin-top: 2%;
			}
			@include ipad(){
				width: 45%;
				margin-left: 3%;
				margin-top: 2%;
			}
			@include iphone8(){
				display: block;
				width: 100%;
				margin-top: 15px;
				margin-left: 0;
			}
		}

		div:nth-of-type(1){
			transform: translateX(30%) translateY(15%) rotate(-5deg);
			z-index: 3;
			transition: .5s;
			@include ipadPro(){
				transform: translateX(0%) translateY(0%);
				z-index: 0;
			}
		}

		div:nth-of-type(1):hover{
			transform: translateX(0%) translateY(0%) rotate(0deg);
			transition: .5s;
		}

		div:nth-of-type(2){
			transform: translateX(-60%) translateY(5%) rotate(-10deg);
			z-index: 2;
			@include ipadPro(){
				transform: translateX(0%) translateY(0%) rotate(0deg);
				z-index: 0;
			}
		}

		div:nth-of-type(3){
			transform: translateX(60%) translateY(-20%) rotate(-5deg);
			z-index: 1;
			@include ipadPro(){
				transform: translateX(0%) translateY(0%) rotate(0deg);
				z-index: 0;
			}
		}

		div:nth-of-type(4){
			transform: translateX(-65%) translateY(-40%) rotate(-20deg);
			z-index: 0;
			@include ipadPro(){
				transform: translateX(0%) translateY(0%) rotate(0deg);
				z-index: 0;
			}
		}

		div:hover ~ div{
			transform: translateX(0%) translateY(0%) rotate(0deg);
			transition: 1s;
		}

		img{
			width: 100%;
			vertical-align: middle;
		}
		@include ipad(){
			width: 100%;
		}
		@include iphone8(){
				width: 100%;
		}
}
/*bsTitle end*/

