.content{
	width: 100%;
	height: 100%;
	border: 2px solid #ddd;
	position: relative;
	h2{
		position: relative;
		margin-top: 50px;
		text-align: center;
		color: #3B1614;
		font-family: 微軟正黑體, sans-serif;
	}
	h2:after{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		width: 160px;
		border-bottom: 3px solid #3B1614;
	}
	p{
		margin: 10px;
		text-align: center;
		font-size: 20px;
		font-family: 微軟正黑體, sans-serif;
		color: #333;
		font-weight: bold;
	}
	button {
		width: 60px;
		height: 30px;
		position: absolute;
		top: 60%;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		font-size: 16px;
		line-height: 32px;
		color: #fff;
		background-color: rgba(111,68,54,1);
		cursor: pointer;
	  text-align: center;   
	  text-decoration: none;
	  outline: none;
		transition-duration: 0.4s;
		box-shadow: 0px 2px 2px 2px rgba(0,0,0,.4);
		border: none;
	}
	button a{
		position: relative;
		text-decoration: none;
		color: #fff;
		padding: 2% 11%;
	}
	button:hover a{
		color: rgba(111,68,54,1);
	}
	button:hover{
		background-color: #fff;
		color: rgba(111,68,54,1);
	}
	button:active{
	  box-shadow: 0 1px 1px 1px #666;
	  transform: translateY(2px);
	}
	button:before{
		content: '';
		display: block;
		width: 90%;
		height: 80%;
		border:1px solid #fff;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
	}
	button:hover:before{
		border: 1px solid rgba(111,68,54,1);
	}
	
	@include desktop(){
		h2{
			margin-top: 60px;
			font-size: 46px;
		}
		p{
			font-size: 32px;
		}
		h2:after{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			width: 230px;
			border-bottom: 3px solid #3B1614;
		}
		button {
			width: 100px;
			height: 50px;
			position: absolute;
			top: 60%;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			font-size: 26px;
			a{
				padding: 2% 11%;
			}
		}
	}

	@media (max-width: 823px){
		h2{
			margin-top: 30px;
		}
		p{
			font-weight: normal;
		}
	}

	@media (max-width: 667px){
		h2{
			margin-top: 30px;
			font-size: 28px;
		}
		p{
			font-size: 18px;
		}
	}

	@include iphone8(){
		h2{
			position: absolute;
			top: -55%;
			left: 33%;
			font-size: 20px;
		}
		h2:after{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			width: 100%;
			border-bottom: 3px solid #3B1614;
		}
		p{
			font-size: 16px;
		}
		button{
			width: 80px;
			height: 40px;
			position: absolute;
			top: 70%;
			left: 0;
			font-size: 18px;
			a{
				padding: 3% 15%;
				font-size: 14px;
			}
		}
	}

	@include iphone5(){
		h2{
			left: 30%;
			font-size: 18px;
		}
		p{
			font-size: 14px;
		}
		button{
			a{
				font-size: 12px;
			}
		}
	}
}

